<template>
    <div class="common-wrap">
        <div class="common-left" :style="{width: (100 - otherVariable.contentWidth) + '%'}" v-show="!otherVariable.isSpread">
            <div class="not-allowed-wrap" v-if="isShowNotAllowed" @click="handleNotAllowed"></div>
            <!---->
            <div class="common-btn-wrap">
                <!--todo-->
                <!--<el-button class="btn-cyan add-btn" @click=""></el-button>-->
                <el-button @click="handleRefresh"></el-button>
            </div>
            <!---->
            <CommonSearch
                :isShowStatus="isShowStatus"
                :propData="searchProp"
                :searchForm="searchForm"
                @getData="getSearchList"
            >
                <template slot="search-other-btn">
                    <el-button @click="handleReset"></el-button>
                </template>
            </CommonSearch>
            <!---->
            <CommonTable
                v-if="isResize"
                ref="commontable"
                :mainHeight="mainHeight"
                :isShowStatus="isShowStatus"
                :propData="tableProp"
                :otherVariable="otherVariable"
                :tableData="tableData"
                @getInitPage="getInitPage"
                @handleDetail="handleDetail"
            >
            </CommonTable>
            <!---->
            <CommonPagination
                :pageObj="pageObj"
                @getData="getData"
            >
            </CommonPagination>
        </div>
        <div class="common-right" :style="{width: otherVariable.contentWidth + '%'}">
            <!--/-->
            <CommonDetailX
                :otherVariable="otherVariable"
                :propData="formProp"
                :detailForm="detailForm"
                @toggleSpread="toggleSpread"
            >
                <!---->
                <template slot="header">
                    <div class="header-form" ref="headForm">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'outter'">
                                <span>{{ item.name ? (item.name + ':') : '' }}</span>
                                <span>{{ left_detailForm[item.prop]===''?'':left_detailForm[item.prop] ===''?'':left_detailForm[item.prop]}}</span>
                            </div>
                        </template>
                    </div>
                </template>
                <!---->
                <template slot="main-btn-wrap">
                    <div class="xs-icon-confirm" v-if="docStatus === 50 && !isNewEdit && isRefuse !== 1" @click="handleAudit" title=""></div>
                    <!--<div class="xs-icon-confirm" v-if="docStatus === 50 && 80<=orderChangeDocStatus&&orderChangeDocStatus<90&& orderDocStatus >= 100" @click="productConfirm" title="" style="z-index: 99; line-height: 1; padding-top: 8px;">  </div>-->
                    <!--<i class="sm-icon-edit el-icon-edit" @click="editMainRefuse" title="" style="right: 100px"></i>-->
                    <div class="xs-icon-confirm" v-if="detailId && !isNewEdit && docStatus < 100 && isRefuse !== 1" @click="editMainRefuse" title="" style="right: 100px;background: #00C26F"></div>
                    <i v-if="detailId && !isNewEdit && isRefuse !== 1" class="icon-print" title="" @click="handlePrint" style="right: 45px;"></i>
                    <i class="sm-icon-edit el-icon-close" @click="cancaleMainRefuse" title="" v-if="isNewEdit" style="right: 115px"></i>
                    <div class="xs-icon-confirm" v-if="isNewEdit" @click="saveMainRefuse" title="" style="right: 40px;line-height: 1; padding-top: 8px;"> </div>
                </template>
                <!---->
                <template slot="head-form-title">
                    <div class="head-form-title">
                        <span>&nbsp;{{ left_detailForm.projectName }}{{ left_detailForm.projectCode?("("+ left_detailForm.projectCode + ")"):"" }}</span>
                        <span></span>
                    </div>
                </template>
                <!---->
                <template slot="head-form-content">
                    <div class="head-form-content">
                        <template v-for="item in formProp">
                            <div v-if="item.type === 'inner' && !item.isLong">
                                <span>{{ item.name }}</span>
                                <span>{{ left_detailForm[item.prop] }}</span>
                            </div>
                        </template>
                    </div>
                    <div class="remark-wrap">
                        <div></div>
                        <div class="headspan scrollBar" :title="left_detailForm.remark">{{ left_detailForm.remark }}</div>
                    </div>
                </template>
                <!---->
                <template slot="common-content">
                    <!--tab-->
                    <el-tabs v-model="activeName" class="common-content-tab">
                        <el-tab-pane label="" name="first">
                            <div v-if="isResize" :style="{height: formHeight + 'px',overflowY: 'auto'}">
                                <div class="tab-form-wrap" style="padding: 30px 0 0 0">
                                    <el-form class="tab-form-item" :model="detailForm" label-width="100px">
                                        <h3 style="text-align: center"></h3>
                                        <!--<el-form-item label="">
                                            <el-date-picker type="date" v-model="detailForm.orderOldDocDate" :disabled="true" style="width: 100%"></el-date-picker>
                                        </el-form-item>-->
                                        <!--<el-form-item label="">
                                            <el-date-picker type="date" v-model="detailForm.orderOldDeliveryDate" :disabled="true" style="width: 100%"></el-date-picker>
                                        </el-form-item>-->
                                        <el-form-item label="">
                                            <el-input v-model="detailForm.orderOldDeliveryDays" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-select v-model="detailForm.orderOldDeliveryLevel" :disabled="true" style="width: 100%">
                                                <el-option
                                                    v-for="(item,index) in deliveryLevelList"
                                                    :key="index"
                                                    :label="item.dictText"
                                                    :value="item.dictValue">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-select v-model="detailForm.orderOldDocType" :disabled="true" style="width: 100%">
                                                <el-option
                                                    v-for="(item,index) in orderTypeList"
                                                    :key="index"
                                                    :label="item.dictText"
                                                    :value="item.dictText">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                        <!--<el-form-item label="">
                                            <el-input v-model="detailForm.orderOldClientCode" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>-->
                                        <el-form-item label="">
                                            <el-input v-model="detailForm.orderOldProjectName" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-input v-model="detailForm.orderOldPackMethodName" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-input v-model="detailForm.orderOldUsrTxt1Name" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <el-form-item label="">
                                            <el-input v-model="detailForm.orderOldRemark" :disabled="true" style="width: 100%" type="textarea" :rows="2"></el-input>
                                        </el-form-item>
                                    </el-form>
                                    <el-form class="tab-form-item" :model="detailForm" label-width="50px">
                                        <h3 style="text-align: center"></h3>
                                        <!---->
                                        <!--<el-form-item label=" ">
                                            <el-date-picker type="date" v-model="detailForm.orderNewDocDate" :disabled="true" style="width: 100%"></el-date-picker>
                                        </el-form-item>-->
                                        <!---->
                                        <!--<el-form-item label=" ">
                                            <el-date-picker type="date" v-model="detailForm.orderNewDeliveryDate" :disabled="true" style="width: 100%"></el-date-picker>
                                        </el-form-item>-->
                                        <!---->
                                        <el-form-item label=" ">
                                            <el-input v-model="detailForm.orderNewDeliveryDays" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <!---->
                                        <el-form-item label=" ">
                                            <el-select v-model="detailForm.orderNewDeliveryLevel" :disabled="true" style="width: 100%">
                                                <el-option
                                                    v-for="(item,index) in deliveryLevelList"
                                                    :key="index"
                                                    :label="item.dictText"
                                                    :value="item.dictValue">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                        <!---->
                                        <el-form-item label=" ">
                                            <el-select v-model="detailForm.orderNewDocType" :disabled="true" style="width: 100%">
                                                <el-option
                                                    v-for="(item,index) in orderTypeList"
                                                    :key="index"
                                                    :label="item.dicttext"
                                                    :value="item.dicttext">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                        <!---->
                                        <!-- <el-form-item label=" ">
                                             <el-input v-model="detailForm.orderNewClientCode" :readonly="true" style="width: 100%"></el-input>
                                         </el-form-item>-->
                                        <!---->
                                        <el-form-item label=" ">
                                            <el-input v-model="detailForm.orderNewProjectName" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <!---->
                                        <el-form-item label=" ">
                                            <el-input v-model="detailForm.orderNewPackMethodName" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <el-form-item label=" ">
                                            <el-input v-model="detailForm.orderNewUsrTxt1Name" :readonly="true" style="width: 100%"></el-input>
                                        </el-form-item>
                                        <!---->
                                        <el-form-item label=" ">
                                            <el-input v-model="detailForm.orderNewRemark" :disabled="true" style="width: 100%" type="textarea" :rows="2"></el-input>
                                        </el-form-item>
                                    </el-form>
                                </div>
                                <el-form :model="detailForm" label-width="100px" ref="refForm" :rules="mainRules">
                                    <el-form-item label="" prop="refuseReason">
                                        <el-input v-model="detailForm.refuseReason" :disabled="!isNewEdit" style="width: 100%" type="textarea" :rows="2"></el-input>
                                    </el-form-item>
                                </el-form>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="" name="second">
                            <el-table
                                v-if="isResize"
                                tooltip-effect="dark"
                                v-loading="loadingI"
                                :data="tabDataI"
                                border
                                show-summary
                                :summary-method="getSummaries"
                                :height="tableHeight"
                                highlight-current-row
                            >
                                <el-table-column label="" align="center" width="40">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.docItem }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left" width="130" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.changeText }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left" width="130" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.itemNo }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.matCode }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx1Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.colorDescribe }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.colorGroupName }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left" :show-overflow-tooltip="true" width="80">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx2Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label=" m" align="right" width="55">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx4Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="right" width="80">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.cx3Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="right" width="55">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.digit }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label=" kg/m" align="right">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.meterWght ? parseFloat(scope.row.meterWght).toFixed(3) : '' }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label=" kg/pcs" align="right">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.unitWght ? parseFloat(scope.row.unitWght).toFixed(3) : '' }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label=" kg" align="right" prop="theoryWght">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.theoryWght ? parseFloat(scope.row.theoryWght).toFixed(3) : '' }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.usrTxt2Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center" width="50">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.usrTxt3Name === 'true' ? '' : '' }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="right" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.usrTxt6Name }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left" width="200" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.remark }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center" width="50">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.isRefuse ? '':'' }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="left" width="100" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <span>{{ scope.row.refuseReason }}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column label="" align="center" width="55" fixed="right">
                                    <template slot-scope="scope" v-if="scope.row.isRefuse !== 1">
                                        <el-dropdown @command="handleCommand($event,scope.$index,tabDataI)">
                                            <span><i class="el-icon-more"></i></span>
                                            <el-dropdown-menu slot="dropdown" class="del-shadow">
                                                <template>
                                                    <el-dropdown-item command="a"></el-dropdown-item>
                                                </template>
                                            </el-dropdown-menu>
                                        </el-dropdown>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-tab-pane>
                    </el-tabs>
                </template>
            </CommonDetailX>
        </div>
        <!---->
        <el-dialog
            title=""
            :visible.sync="refuseVisible"
            width="30%">
            <el-form :model="refuseForm" ref="refChildForm" :rules="childRules" @submit.native.prevent>
                <el-form-item label="" prop="refuseReason">
                    <el-input v-model.trim="refuseForm.refuseReason"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancelRefuseItem"> </el-button>
                <el-button class="btn-cyan add-btn" @click="refuseItem"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import CommonMixins from '$src/components/CommonMixins'
    import CommonSearch from '$src/components/CommonSearch'
    import CommonTable from '$src/components/CommonTable'
    import CommonPagination from '$src/components/CommonPagination'
    import CommonDetailX from '$src/components/CommonDetailX'
    export default {
        mixins: [CommonMixins],
        components: {
            CommonSearch,
            CommonTable,
            CommonPagination,
            CommonDetailX
        },
        data() {
            return {
                formHeight: 526,
                tableHeight: this.$store.state.screenHei*0.65,
                mainHeight: 716,
                isResize: true,
                isShowNotAllowed:false,
                operateType: 'add',
                isNewEdit: false,
                isShowStatus: {
                    isMore: false,
                    isRead: true,
                    isPopUp: false
                },
                otherVariable: {
                    isSpread: false,
                    contentWidth: 70,
                    detailTitle: '',
                    addTitle: '',
                    popupTitle: '',
                    isMulSelect: true,
                    isInlineForm: true,
                    isEdit: false,
                    isChildEdit: false,
                    isHeadComplex: true,
                    isFormComplex: true,
                    loading: false,
                    $isCustom: true,
                    isNotValidate: true,
                    isReadTable: true
                },
                tempWidth: null,
                tableData: [],
                //
                searchProp: [
                    {
                        name:'',
                        prop:'orderDocCode',
                        type:'normal',
                        isMainSearch:true,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'orderChangeCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false,
                        placeholder:''
                    },
                    {
                        name:'',
                        prop:'clientCode',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'clientName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'docStatus',
                        type:'select',
                        selectList:[{value:null,label:''},{value:50,label:''},{value:100,label:''}],
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateStart',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'orderDocDateEnd',
                        type:'date',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'companyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                    {
                        name:'',
                        prop:'supplyCompanyName',
                        type:'normal',
                        isMainSearch:false,
                        isMain:false
                    },
                ],
                //
                formProp: [
                    {name:'',prop:'orderChangeCode',type:'outter'},
                    {name:'',prop:'docDate',type:'outter'},
                    {name:'',prop:'docStatusName',type:'outter'},
                    {name:'',prop:'clientCode',type:'inner'},
                    {name:'',prop:'clientCode2',type:'inner'},
                    {name:'',prop:'orderDocCode',type:'inner'},
                    {name:'',prop:'remark',type:'inner',isLong:true}
                ],
                //
                mainRules: {
                    refuseReason: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                childRules: {
                    refuseReason: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                //
                tableProp: [
                    {
                        name:'fid',
                        prop:'fid',
                        type:'normal',
                        isMain:true
                    },
                    {
                        name1:'/',
                        prop1:'orderChangeCode',
                        name2:'',
                        prop2:'docStatusName',
                        status: 'docStatus',
                        type:'mixinStatus',
                        width: '115',

                    },
                    {
                        name1:'/',
                        prop1:'orderDocCode',
                        name2:'',
                        prop2:'docDate',
                        type:'mixin',
                        width: '120'
                    },
                    {
                        name1:'/',
                        prop1:'clientCode',
                        name2:'',
                        prop2:'clientCode2',
                        type:'mixin',
                        width: '100'
                    },
                    {
                        name1:'/',
                        prop1:'docCode',
                        name2:'',
                        prop2:'projectName',
                        type:'mixin'
                    }
                ],
                searchForm: {docStatus: 50},
                detailForm: {},
                left_detailForm: {},
                copyForm: {},
                tempForm: {},
                submitRow: {},
                detailId: null,
                isRefuse: null,
                orderDocStatus: null,
                orderChangeId: null,
                orderChangeDocStatus: null,
                docCode: null,
                docStatus: null,
                pageObj: {
                    commonPage: 1,
                    commonSize: 10,
                    commonSizes: [10, 20, 30, 40, 50, 100],
                    commonTotal: 0
                },
                //
                refuseVisible: false,
                refuseForm: {},
                //
                tabLeft: '300px',
                activeName: 'first',
                childDetailRow: {},
                focusTimes: 1,
                editIndex: 0,
                //
                tabDataI: [],
                copyDataI: [],
                loadingI: false,
                //
                orderTypeList: [],
                //
                deliveryLevelList: [],
                //
                packMethodList: [],
            }
        },
        watch: {
            'isNewEdit':{
                handler(val) {
                    this.isShowNotAllowed = val;
                }
            }
        },
        mounted() {
            this.getData();
            this.getOrderType();
            this.getDeliveryLevel();
            // /
            this.listenResizeII();
            this.changeSizeII();
            this.tempWidth = this.otherVariable.contentWidth;
            this.mainRules = {refuseReason: []}
        },
        methods: {
            //
            getData(isFirst = true) {
                if(this.searchForm.orderDocCode) {
                    this.searchForm = {orderDocCode:this.searchForm.orderDocCode}
                }else if(this.searchForm.orderChangeCode){
                    this.searchForm = {orderChangeCode:this.searchForm.orderChangeCode}
                }else {
                    this.searchForm.orderDocCode = null;
                    this.searchForm.orderChangeCode = null;
                }
                this.tableData = [];
                this.fetch(`/oms/sls/SalesOrderChange/getChangeMasterFlow?pageNo=${this.pageObj.commonPage}&pageSize=${this.pageObj.commonSize}`,this.searchForm)
                    .then(result => {
                        if(result.data && result.data.list.length) {
                            this.tableData = result.data.list;
                            this.pageObj.commonTotal = result.data.total;
                            if(isFirst) {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.tableData[0].fid);
                                })
                            }else {
                                this.$nextTick(() => {
                                    this.$refs.commontable.renderRow(this.detailId);
                                })
                            }
                        }else {
                            this.pageObj.commonTotal = 0;
                            this.detailId = null;
                            this.isRefuse = null;
                            this.tabDataI = [];
                            this.copyDataI = [];
                            this.detailForm = {};
                            this.copyForm = {};
                            this.left_detailForm = {};
                            this.orderDocStatus = null;
                            this.orderChangeId = null;
                            this.orderChangeDocStatus = null;
                        }
                    })
            },
            getInitPage() {
                this.pageObj.commonPage = 1;
                this.pageObj.commonSize = 10;
                this.getData();
            },
            //
            handleRefresh() {
                this.detailForm = {};
                this.copyForm = {};
                this.left_detailForm = {};
                this.getData();
                this.otherVariable.isChildEdit = false;
                this.tabDataI = [];
            },
            //
            handleReset() {
                this.searchForm = {docStatus: 50};
                this.handleRefresh();
            },
            //
            changeRefresh() {
                this.getData(false);
                this.otherVariable.isChildEdit = false;
            },
            //
            handleDetail(row) {
                this.mainRules = {};
                this.otherVariable.isChildEdit = false;
                this.tabDataI = [];
                this.copyDataI = [];
                this.isShowStatus.isRead = true;
                this.detailForm = {};
                this.copyForm = {};
                this.left_detailForm = row;
                this.copyForm = {};
                this.fetch('/oms/sls/SalesOrderChange/getChangeMaster',{fid:row.fid})
                    .then(result => {
                        if(result.status === 200 && result.data && result.data.list.length) {
                            this.detailForm = result.data.list[0];
                            this.copyForm = this.$lodash.cloneDeep(result.data.list[0]);
                        }else {
                            this.detailForm = {};
                            this.copyForm = {};
                        }
                    });
                this.getChildTable(row.fid);
                this.detailId = row.fid;
                this.isRefuse = row.isRefuse;
                this.orderDocStatus = row.orderDocStatus;
                this.orderChangeId = row.orderChangeId;
                this.orderChangeDocStatus = row.orderChangeDocStatus;
                this.docCode = row.docCode;
                this.docStatus = row.docStatus;
            },
            getSearchList(){
                this.pageObj.commonPage=1;
                this.pageObj.commonSize=10;
                this.getData();
            },
            //
            editMainRefuse() {
                this.isNewEdit = true;
                this.mainRules = {refuseReason: [{required: true, message: '', trigger: 'blur'}]}
            },
            cancaleMainRefuse() {
                this.mainRules = {};
                this.$refs.refForm.resetFields();
                this.isNewEdit = false;
                this.detailForm = this.$lodash.cloneDeep(this.copyForm);
            },
            saveMainRefuse() {
                this.$refs.refForm.validate((valid) => {
                    if(valid) {
                        this.fetch('/oms/sls/SalesOrderChange/refuseOrderChange',this.detailForm)
                            .then(result => {
                                if(result.status === 200) {
                                    this.$message({
                                        message: result.message,
                                        type: 'success'
                                    });
                                    this.isNewEdit = false;
                                    this.getData(false);
                                }else {
                                    this.$message({
                                        message: this.returnErrorMsg(result.error),
                                        type: 'error'
                                    })
                                }
                            })
                    }else {
                        this.$message({
                            message: '',
                            type: 'warning'
                        })
                    }
                })
            },
            //
            handleAudit() {
                let tips = '';
                /*if(this.orderDocStatus < 80) {
                    tips = '<strong><i></i></strong>'
                }*/
                if(this.detailId) {
                    this.$confirm(`${tips}`, '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning',
                        dangerouslyUseHTMLString: true
                    }).then(() => {
                        this.getAjax(`/oms/sls/SalesOrderChange/complaintOrAuditOrderChangeFlowById?orderChangeFlowId=${this.detailId}`)
                        .then(result => {
                            if (result.status === 200) {
                                this.$message({
                                    message: result.message,
                                    type: 'success'
                                });
                                this.getData(false);
                            }else {
                                this.$message({
                                    message: this.returnErrorMsg(result.error),
                                    type: 'error'
                                })
                            }
                        })
                    }).catch(() => {
                        console.log('cnacel')
                    })

                }else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            },
            //
            handlePrint() {
                let isReturn = false;
                let resultUrl = '';
                this.getAjax(`/oms/PrintController/isPrintOrderChange?orderChangeFlowId=${this.detailId}`)
                    .then(result => {
                        if(result.status === 200) {
                            isReturn = true;
                            resultUrl = result.data;
                        }else {
                            isReturn = false;
                            this.$message({
                                type:'error',
                                message: result.error || ''
                            })
                        }
                    })
                    .then(() => {
                        if(isReturn) {
                            window.open(resultUrl);
                        }
                    })
            },
            //
            productConfirm() {
                if(this.orderChangeId) {
                    this.$confirm('?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.getAjax(`/oms/sls/SalesOrderChange/complaintOrderChangeMasterById?orderChangeMasterId=${this.orderChangeId}`)
                            .then(result => {
                                if (result.status === 200) {
                                    this.$message({
                                        message: result.message,
                                        type: 'success'
                                    });
                                    this.getInitPage();
                                }else {
                                    this.$message({
                                        message: this.returnErrorMsg(result.error),
                                        type: 'error'
                                    })
                                }
                            })
                    }).catch(() => {
                        console.log('cnacel')
                    })
                }else {
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            },
            //
            getChildTable(fid) {
                this.loadingI = true;
                this.isShowNotAllowed = true;
                this.getAjax(`/oms/sls/SalesOrderChange/getOrderChangeDetail?pageSize=1000&orderChangeMasterId=${fid}`)
                    .then(result => {
                        this.loadingI = false;
                        this.isShowNotAllowed = false;
                        if(result.data && result.data.list) {
                            result.data.list.forEach(item => {
                                if(item.usrTxt3Name) {
                                    item.usrTxt3Name = item.usrTxt3Name.toLowerCase();
                                }
                            });
                            this.tabDataI = result.data.list;
                            this.copyDataI = this.$lodash.cloneDeep(this.tabDataI);
                        }
                    });
            },
            handleCommand(command,index,rows) {
                switch(command)
                {
                    case 'a':
                        this.refuseForm = rows[index];
                        this.refuseVisible = true;
                        break;
                }
            },
            //
            refuseItem() {
                let tempParam = [{
                    fid: this.refuseForm.fid,
                    masterId: this.refuseForm.masterId,
                    refuseReason: this.refuseForm.refuseReason
                }];
                this.$refs.refChildForm.validate((valid) => {
                    if(valid) {
                        this.fetch('/oms/sls/SalesOrderChange/refuseDetail',tempParam)
                            .then(result => {
                                if(result.status === 200) {
                                    this.$message({
                                        message: result.message,
                                        type: 'success'
                                    });
                                    this.refuseVisible = false;
                                    this.getChildTable(this.detailId);
                                }else {
                                    this.$message({
                                        message: this.returnErrorMsg(result.error),
                                        type: 'error'
                                    })
                                }
                            })
                    }else {
                        console.log('error submit!!');
                        return false;
                    }
                })
            },
            cancelRefuseItem() {
                this.refuseVisible = false;
                this.tabDataI = this.$lodash.cloneDeep(this.copyDataI);
            },
            //
            getSummaries(param) {
                const { columns, data } = param;
                const sums = [];
                let digit = null;
                let theoryWght = null;
                if(data.length) {
                    data.forEach((d,i) => {
                        if(!d.parentVariantId) {
                            digit += Number(d.digit);
                            theoryWght += Number(d.theoryWght);
                        }
                    })
                }
                sums[0] = '';
                //
                sums[10] = digit;
                //
                theoryWght ? sums[13] = theoryWght.toFixed(3) : 0;
                return sums;
            },
            //
            handleNotAllowed() {
                if(this.isNewEdit) {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.cancaleMainRefuse();
                    }).catch(() => {
                        console.log('cnacel')
                    })
                }
            },

            //-------------------  START -------------------//

            //
            getMdmPackMethod(val) {
                this.packMethodList = [];
                if(val) {
                    this.fetch('/oms/BasedataController/getMdmPackMethod?pageSize=10',{fname:val})
                        .then(result => {
                            if(result.data && result.data.list) {
                                this.packMethodList = result.data.list;
                            }
                        })
                }
            },

            //
            getOrderType() {
                this.orderTypeList = [];
                this.getAjax('/oms/sys/DataDictionary/getOrderType?pageSize=10')
                    .then(result => {
                        if(result.data) {
                            this.orderTypeList = result.data;
                        }
                    })
            },

            //
            getDeliveryLevel() {
                this.deliveryLevelList = [];
                this.getAjax('/basedata/sys/DataDictionary/getDataDictionary?docCode=30116')
                    .then(result => {
                        if(result.data) {
                            result.data.forEach(item => {
                                item.dictValue = Number(item.dictValue)
                            });
                            this.deliveryLevelList = result.data;
                        }else {
                            this.deliveryLevelList = [];
                        }
                    })
            }

            //-------------------  END -------------------//
        }
    }
</script>

<style lang="less" scoped>
    .popup-enter-active, .popup-leave-active {
        transition: left 0.1s;
        left: 0;
    }
    .popup-enter, .popup-leave-to {
        left: 100%;
    }
</style>



